import React, { useState, useEffect } from 'react';

const About = () => {
  const [count, setCount] = useState(0);
  useEffect(() => {
     console.log('effect used');
  });
  return (
    <div onClick={() => { setCount(count + 1) }}>
      This is the about page, count: {count}
    </div>
  );
};

export default About;